<template>
  <div class="MemoryFlow">
    <Header></Header>
    <router-link class="search" :to="{name:'Search'}">
      
    </router-link>
    <el-collapse v-model="activeNames" class="list">
      <el-collapse-item title=" " name="1">
        <el-collapse v-model="activeNamesOne">
          <el-collapse-item title=" " name="1">
            <Content :data="data1" :text="text1"></Content>
          </el-collapse-item>
          <el-collapse-item title=" " name="2">
            <Content :data="data2" :text="text2"></Content>
          </el-collapse-item>
           <el-collapse-item title=" " name="3">
            <Content :data="data3" :text="text3"></Content>
          </el-collapse-item>
          <el-collapse-item title=" " name="4">
            <Content :data="data4" :text="text4"></Content>
          </el-collapse-item>
        </el-collapse>
      </el-collapse-item>
      <el-collapse-item title=" " name="2">
        <el-collapse v-model="activeNamesOne">
          <el-collapse-item title=" " name="5">
            <Content :data="data5" :text="text5"></Content>
          </el-collapse-item>
          <el-collapse-item title=" " name="6">
            <Content :data="data6" :text="text6"></Content>
          </el-collapse-item>
          <el-collapse-item title=" " name="7">
            <Content :data="data7" :text="text7"></Content>
          </el-collapse-item>
          <el-collapse-item title=" " name="8">
            <Content :data="data8" :text="text8"></Content>
          </el-collapse-item>
        </el-collapse>
      </el-collapse-item>
    </el-collapse>
    <Footer :shownum="shownum"></Footer>
  </div>
</template>
<script>
import axios from 'axios'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import Content from '@/components/Content'
import constitution1 from '@/components/constitution1'
import constitution2 from '@/components/constitution2'
import constitution3 from '@/components/constitution3'
import constitution4 from '@/components/constitution4'
import constitution5 from '@/components/constitution5'
import constitution6 from '@/components/constitution6'
import constitution7 from '@/components/constitution7'
import constitution8 from '@/components/constitution8'
export default {
  name: 'MemoryFlow',
  components:{
    Header,Footer,Content
  },
  data () {
    return {
      msg: '',
      shownum:2,
      activeNames: ['2'],
      activeNamesOne: ['8']
    }
  },
  created () {
    this.data1 = constitution1.data
    this.text1 = constitution1.node
    this.data2 = constitution2.data
    this.text2 = constitution2.node
    this.data3 = constitution3.data
    this.text3 = constitution3.node
    this.data4 = constitution4.data
    this.text4 = constitution4.node
    this.data5 = constitution5.data
    this.text5 = constitution5.node
    this.data6 = constitution6.data
    this.text6 = constitution6.node
    this.data7 = constitution7.data
    this.text7 = constitution7.node
    this.data8 = constitution8.data
    this.text8 = constitution8.node
  },
  mounted () {
      axios({
        method: 'get',
        header: {'Content-Type': 'application/json'},
        url: '/api/test.json'
      }).then(response => {
        console.log(response)
      })
  },
  methods: {

  }
}
</script>
<style scoped>
  .MemoryFlow .list {
    text-align: left;
    padding: 10px;
  }
  .search{
    width:90%;
    height: 30px;
    color: #cccccc;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:10px auto;
    outline:none;
    text-decoration: none;
    border: 1px solid #ccc;
  }
</style>


